*{
  direction: rtl;
}
#firstpar{
  font-family: 'SecularOne-Regular';
  text-align: center;
  font-size: 3em;
  font-weight: 800;
}
#secondpar{
  font-family: 'Heebo';
  text-align: center;
  font-weight: lighter;
}
#thirdpar{
  font-family: 'Heebo';
  text-align: center;
  font-weight: 200;
}

#inputsdiv{
  text-align: center;
}
input{
  align-self: center;
  background-color: #f2c442;
  border: none;
  padding:0.5em 1em;
  margin-bottom: 0.3em;
  width: 20em;
}
#inputsdiv button{
  margin-top: 0.5em;
  font-family: 'Heebo';
  letter-spacing: 0.1em;
  background-color: #fe6060;
  border: none;
  padding: 0.4em 5em;
  color:#fac900;
  font-weight: 600;

}

#inputsdiv button:active{
  transform: scale(0.92);
  transition: all 0.1s ease;
}

input::-webkit-input-placeholder{
  font-style: italic;
  font-weight: 800;
  color:#fe6060;
  opacity: 1;
}

input:-ms-input-placeholder{
  font-style: italic;
  font-weight: 800;
  color:#fe6060;
  opacity: 1;
}

input::-ms-input-placeholder{
  font-style: italic;
  font-weight: 800;
  color:#fe6060;
  opacity: 1;
}

input::placeholder{
  font-style: italic;
  font-weight: 800;
  color:#fe6060;
  opacity: 1;
}

input:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color:#fe6060;
  font-weight: 800;
  font-style: italic;
}

input::-ms-input-placeholder { /* Microsoft Edge */
  color:#fe6060;
  font-weight: 800;
  font-style: italic;
}


